"use client";

import { useState, useEffect } from "react";
import {
  Users,
  UserCheck,
  UserX,
  Search,
  Download,
  Plus,
  Edit,
  Award,
  ArrowUpDown,
  Trash,
} from "lucide-react";
import axios from "axios";
import AddStudentModel from "./AddStudentModel";
import ExportModal from "./ExportModal";
import {
  StudentProvider,
  useStudentContext,
} from "../../context/StudentContext.";

function StudentManagementContent() {
  const {
    students,
    setStudents,
    showAddStudentModal,
    setShowAddStudentModal,
    showEditStudentModal,
    showExportModal,
    setShowExportModal,
    setStudentFormData,
    setShowEditStudentModal,
    setEditingStudent,
  } = useStudentContext();

  const [selectedCategory, setSelectedCategory] = useState("all");
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedBranch, setSelectedBranch] = useState("all");
  const [selectedBatch, setSelectedBatch] = useState("all");
  const [sortBy, setSortBy] = useState("name");
  const [sortOrder, setSortOrder] = useState("asc");

  const handleEditStudent = (student) => {
    setEditingStudent(student);
    setStudentFormData({
      enrollment_number: student.enrollment_number || "",
      first_name: student.first_name || "",
      last_name: student.last_name || "",
      phone: student.phone || "",
      alternate_phone: student.alternate_phone || "",
      college_email: student.college_email || "",
      personal_email: student.personal_email || "",
      department: student.department || "",
      branch: student.branch || "",
      batch_year: student.batch_year || new Date().getFullYear(),
      current_semester: student.current_semester || 1,
      cgpa: student.cgpa || "",
      backlogs: student.backlogs || 0,
      resume_url: student.resume_url || "",
      linkedin_url: student.linkedin_url || "",
      github_url: student.github_url || "",
      date_of_birth: student.date_of_birth
        ? student.date_of_birth.split("T")[0]
        : "",
      gender: student.gender || "",
      registration_number: student.registration_number || "",
      class_10_percentage: student.class_10_percentage || "",
      class_12_percentage: student.class_12_percentage || "",
      permanent_address: student.permanent_address || "",
      permanent_city: student.permanent_city || "",
      permanent_state: student.permanent_state || "",
      ps2_company_name: student.ps2_company_name || "",
      ps2_project_title: student.ps2_project_title || "",
      ps2_certificate_url: student.ps2_certificate_url || "",
      placement_status: student.placement_status || "eligible",
    });
    setShowEditStudentModal(true);
  };

  const getFilteredStudents = () => {
    let filtered = [...students];

    if (selectedCategory !== "all") {
      filtered = filtered.filter(
        (student) => student.placement_status === selectedCategory
      );
    }

    if (selectedBranch !== "all") {
      filtered = filtered.filter(
        (student) => student.branch === selectedBranch
      );
    }

    if (selectedBatch !== "all") {
      filtered = filtered.filter(
        (student) => student.batch_year.toString() === selectedBatch
      );
    }

    if (searchTerm) {
      filtered = filtered.filter(
        (student) =>
          formatStudentName(student)
            .toLowerCase()
            .includes(searchTerm.toLowerCase()) ||
          student.registration_number
            ?.toLowerCase()
            .includes(searchTerm.toLowerCase()) ||
          student.college_email
            ?.toLowerCase()
            .includes(searchTerm.toLowerCase())
      );
    }

    // Sorting logic
    filtered.sort((a, b) => {
      let aValue, bValue;

      switch (sortBy) {
        case "name":
          aValue = formatStudentName(a);
          bValue = formatStudentName(b);
          break;
        case "rollNo":
          aValue = a.registration_number || "";
          bValue = b.registration_number || "";
          break;
        case "cgpa":
          aValue = a.cgpa || 0;
          bValue = b.cgpa || 0;
          break;
        case "package":
          aValue = (a.current_offer?.package || 0) / 100000;
          bValue = (b.current_offer?.package || 0) / 100000;
          break;
        default:
          aValue = formatStudentName(a);
          bValue = formatStudentName(b);
      }

      return sortOrder === "asc"
        ? aValue > bValue
          ? 1
          : -1
        : aValue < bValue
          ? 1
          : -1;
    });

    return filtered;
  };

  // Fetch students data
  useEffect(() => {
    const fetchStudents = async () => {
      try {
        const response = await axios.get("http://localhost:5000/students");
        setStudents(response.data);
      } catch (err) {
        console.error(err);
      }
    };

    fetchStudents();
  }, []); // Refetch when academic year changes

  // Helper function to format student name
  const formatStudentName = (student) => {
    return `${student.first_name} ${student.last_name}`;
  };

  const getStatistics = () => {
    const totalStudents = students.length;
    const placedStudents = students.filter(
      (s) => s.placement_status === "placed"
    ).length;
    const unplacedStudents = students.filter(
      (s) => s.placement_status === "unplaced"
    ).length;
    const multipleOffersStudents = students.filter(
      (s) => Array.isArray(s.offers_received) && s.offers_received.length > 1
    ).length;
    const debarredStudents = students.filter(
      (s) => s.placement_status === "debarred"
    ).length;
    const higher_studiesStudents = students.filter(
      (s) => s.placement_status === "higher_studies"
    ).length;
    const entrepreneurshipStudents = students.filter(
      (s) => s.placement_status === "entrepreneurship"
    ).length;

    const validStudents = students.filter(
      (s) => s.current_offer && s.current_offer.package
    );

    // Declare defaults
    let highestPackage = 0;
    let lowestPackage = 0;
    let avgPackage = 0;
    let medianPackage = 0;

    if (validStudents.length > 0) {
      const packages = validStudents.map(
        (s) => s.current_offer.package / 100000
      );
      packages.sort((a, b) => a - b); // sort ascending

      // Highest (last element)
      highestPackage = packages[packages.length - 1];

      // Lowest (first element)
      lowestPackage = packages[0];

      // Average
      avgPackage = packages.reduce((acc, p) => acc + p, 0) / packages.length;

      // Median
      const mid = Math.floor(packages.length / 2);
      if (packages.length % 2 === 0) {
        medianPackage = (packages[mid - 1] + packages[mid]) / 2;
      } else {
        medianPackage = packages[mid];
      }
    }

    return {
      totalStudents,
      placedStudents,
      unplacedStudents,
      multipleOffersStudents,
      debarredStudents,
      higher_studiesStudents,
      entrepreneurshipStudents,
      avgPackage: avgPackage.toFixed(1),
      highestPackage,
      medianPackage,
      lowestPackage,
    };
  };

  const stats = getStatistics();

  const getStatusColor = (status) => {
    switch (status) {
      case "placed":
        return "bg-green-100 text-green-800";
      case "unplaced":
        return "bg-neutral-200 text-black-800";
      case "higher_studies":
        return "bg-blue-100 text-blue-800";
      case "entrepreneurship":
        return "bg-purple-100 text-purple-800";
      case "debarred":
        return "bg-red-100 text-red-800  ";
    }
  };

  const branches = [...new Set(students.map((s) => s.branch))];
  const batches = [...new Set(students.map((s) => s.batch))];
  const filteredStudents = getFilteredStudents();

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-7xl mx-auto space-y-6">
        {/* Header */}
        <div className="flex justify-between items-center">
          <div>
            <div className="flex items-center gap-4">
              <h1 className="text-3xl font-bold text-gray-800">
                Student Management
              </h1>
            </div>
            <p className="text-gray-600 mt-1">
              Manage and categorize students based on placement status and
              college rules
            </p>
          </div>
          <div className="flex space-x-3">
            <button
              onClick={() => setShowExportModal(true)}
              className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 flex items-center space-x-2"
            >
              <Download className="h-4 w-4" />
              <span>Export Data</span>
            </button>
            <button className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 flex items-center space-x-2">
              <Plus className="h-4 w-4" />
              <span>Import Data</span>
            </button>
            <button className="bg-purple-600 text-white px-4 py-2 rounded-lg hover:bg-purple-700 flex items-center space-x-2">
              <Plus className="h-4 w-4" />
              <span>Update Data</span>
            </button>
            <button
              onClick={() => setShowAddStudentModal(true)}
              className="bg-amber-600 text-white px-4 py-2 rounded-lg hover:bg-amber-700 flex items-center space-x-2"
            >
              <Plus className="h-4 w-4" />
              <span>Add Student</span>
            </button>
          </div>
        </div>

        {/* Add/Edit Student Modal */}
        {(showAddStudentModal || showEditStudentModal) && <AddStudentModel />}

        {/* Export Modal */}
        {showExportModal && <ExportModal filteredStudents={filteredStudents} />}

        {/* Statistics Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-6 gap-4">
          <div className="bg-white p-6 rounded-lg shadow-sm border">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">
                  Total Students
                </p>
                <p className="text-2xl font-bold text-gray-900">
                  {stats.totalStudents}
                </p>
              </div>
              <Users className="h-8 w-8 text-blue-600" />
            </div>
          </div>

          <div className="bg-white p-6 rounded-lg shadow-sm border">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">
                  Total Interested
                </p>
                <p className="text-sm font-medium text-gray-600">
                  (for Placement)
                </p>
                <p className="text-2xl font-bold text-gray-900">
                  {stats.totalStudents - 2}
                </p>
              </div>
              <Users className="h-8 w-8 text-blue-600" />
            </div>
          </div>

          <div className="bg-white p-6 rounded-lg shadow-sm border">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Placed</p>
                <p className="text-2xl font-bold text-green-600">
                  {stats.placedStudents}
                </p>
              </div>
              <UserCheck className="h-8 w-8 text-green-600" />
            </div>
          </div>

          <div className="bg-white p-6 rounded-lg shadow-sm border">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Unplaced</p>
                <p className="text-2xl font-bold text-red-600">
                  {stats.unplacedStudents}
                </p>
              </div>
              <UserX className="h-8 w-8 text-red-600" />
            </div>
          </div>

          <div className="bg-white p-6 rounded-lg shadow-sm border">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Avg CTC</p>
                <p className="text-xl font-bold text-purple-600">
                  {stats.avgPackage} LPA
                </p>
                <p className="text-xs text-gray-500">
                  Highest: {stats.highestPackage} LPA
                </p>
                <p className="text-xs text-gray-500">
                  Medium: {stats.medianPackage} LPA
                </p>
                <p className="text-xs text-gray-500">
                  Lowest: {stats.lowestPackage} LPA
                </p>
              </div>
              <Award className="h-8 w-8 text-purple-600" />
            </div>
          </div>
        </div>

        {/* Category Filters */}
        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <div className="flex flex-wrap items-center gap-4">
            <div className="flex items-center space-x-2">
              <span className="text-sm font-medium text-gray-700">
                Category:
              </span>
              <div className="flex space-x-2">
                {[
                  {
                    key: "all",
                    label: "All Students",
                    count: stats.totalStudents,
                  },
                  {
                    key: "placed",
                    label: "Placed",
                    count: stats.placedStudents,
                  },
                  {
                    key: "unplaced",
                    label: "Unplaced",
                    count: stats.unplacedStudents,
                  },
                  {
                    key: "multiple-offers",
                    label: "Multiple Offers",
                    count: stats.multipleOffersStudentss,
                  },
                  {
                    key: "higher_studies",
                    label: "Higher Studies",
                    count: stats.higher_studiesStudents,
                  },
                  {
                    key: "entrepreneurship",
                    label: "Entrepreneurship",
                    count: stats.entrepreneurshipStudents,
                  },
                  {
                    key: "debarred",
                    label: "Debarred",
                    count: stats.debarredStudents,
                  },
                ].map((category) => (
                  <button
                    key={category.key}
                    onClick={() => setSelectedCategory(category.key)}
                    className={`px-3 py-2 rounded-lg text-sm font-medium transition-colors ${
                      selectedCategory === category.key
                        ? "bg-blue-100 text-blue-700 border border-blue-200"
                        : "bg-gray-100 text-gray-600 hover:bg-gray-200"
                    }`}
                  >
                    {category.label} ({category.count})
                  </button>
                ))}
              </div>
            </div>
          </div>
        </div>

        {/* Search and Filters */}
        <div className="bg-white p-4 rounded-lg shadow-sm border">
          <div className="flex flex-wrap gap-4 items-center">
            <div className="flex-1 min-w-64">
              <div className="relative">
                <Search className="h-4 w-4 absolute left-3 top-3 text-gray-400" />
                <input
                  type="text"
                  placeholder="Search by name, roll number, email, or company..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>
            </div>

            <select
              value={selectedBranch}
              onChange={(e) => setSelectedBranch(e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
            >
              <option value="all">All Branches</option>
              {branches.map((branch) => (
                <option key={branch} value={branch}>
                  {branch}
                </option>
              ))}
            </select>

            <select
              value={selectedBatch}
              onChange={(e) => setSelectedBatch(e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
            >
              <option value="all">All Batches</option>
              {batches.map((batch) => (
                <option key={batch} value={batch}>
                  {batch}
                </option>
              ))}
            </select>

            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
            >
              <option value="name">Sort by Name</option>
              <option value="rollNo">Sort by Roll No</option>
              <option value="cgpa">Sort by CGPA</option>
              <option value="package">Sort by Package</option>
            </select>

            <button
              onClick={() => setSortOrder(sortOrder === "asc" ? "desc" : "asc")}
              className="px-3 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 flex items-center space-x-1"
            >
              <ArrowUpDown className="h-4 w-4" />
              <span className="text-sm">
                {sortOrder === "asc" ? "ASC" : "DESC"}
              </span>
            </button>
          </div>
        </div>

        {/* Students Table */}
        <div className="bg-white rounded-lg shadow-sm border overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    <input type="checkbox" className="rounded" />
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Student Details
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Academic Info
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Contact details
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Placement Details
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    BOARD MARKS
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredStudents.map((student) => {
                  return (
                    <tr key={student.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-nowrap">
                        <input type="checkbox" className="rounded" />
                      </td>
                      <td className="px-6 py-4">
                        <div className="flex items-center">
                          <div>
                            <div className="text-sm font-medium text-gray-900">
                              {formatStudentName(student)}
                            </div>
                            <div className="text-sm text-gray-500">
                              {student.registration_number}
                            </div>
                            <div className="text-xs text-gray-400">
                              {student.college_email}
                            </div>
                            <div className="text-xs text-gray-400">
                              {student.personal_email}
                            </div>
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4">
                        <div>
                          <div className="text-sm text-gray-900">
                            {student.department} {student.branch}
                          </div>
                          <div className="text-sm text-gray-500">
                            CGPA:{" "}
                            {student.cgpa !== undefined && student.cgpa !== null
                              ? Number(student.cgpa).toFixed(2)
                              : "N/A"}
                          </div>
                          <div className="text-xs text-gray-400">
                            Backlogs: {student.backlogs} | Batch:{" "}
                            {student.batch_year}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4">
                        <div className="text-sm">+91 {student.phone}</div>
                        {student.alternate_phone && (
                          <div className="text-sm">
                            +91 {student.alternate_phone}
                          </div>
                        )}
                      </td>
                      <td className="px-6 py-4">
                        <div
                          className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getStatusColor(
                            student.placement_status
                          )}`}
                        >
                          {student.placement_status.replace("_", " ")}
                        </div>
                        {student.current_offer && (
                          <div className="mt-1 text-sm text-gray-600">
                            {student.current_offer.company_name}
                          </div>
                        )}
                      </td>
                      <td className="px-6 py-4">
                        {student.offers_received?.length > 0 ? (
                          <div>
                            <div className="text-sm font-medium">
                              {student.offers_received.length} Offer(s)
                            </div>
                            {student.offers_received
                              .slice(0, 2)
                              .map((offer, idx) => (
                                <div
                                  key={idx}
                                  className="text-xs text-gray-500"
                                >
                                  {offer.company_name} -{" "}
                                  {(offer.package / 100000).toFixed(1)} LPA
                                </div>
                              ))}
                          </div>
                        ) : (
                          <span className="text-sm text-gray-400">
                            No offers yet
                          </span>
                        )}
                      </td>
                      <td className="px-6 py-4">
                        <div className="text-sm">
                          <div>10th: {student.class_10_percentage}%</div>
                          <div>12th: {student.class_12_percentage}%</div>
                          {student.drives_skipped > 0 && (
                            <div className="text-red-500">
                              Drives Skipped: {student.drives_skipped}
                            </div>
                          )}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                        <div className="flex space-x-2">
                          <button
                            onClick={() => handleEditStudent(student)}
                            className="text-green-600 hover:text-green-800"
                          >
                            <Edit className="h-4 w-4" />
                          </button>
                          <button className="text-red-500 hover:text-red-700">
                            <Trash className="h-4 w-4" />
                          </button>
                        </div>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>

        {/* Summary */}
        <div className="text-center text-sm text-gray-500">
          Showing {filteredStudents.length} of {students.length} students
        </div>
      </div>
    </div>
  );
}

// Wrap the component with StudentProvider
export default function StudentManagement() {
  return (
    <StudentProvider>
      <StudentManagementContent />
    </StudentProvider>
  );
}
